import {createSlice} from "@reduxjs/toolkit";
import {apiCall} from "../apiAction";

const slice = createSlice({
    name: 'teacherCourse',
    initialState: {teacherCourses: []},
    reducers:{
        getAllTeacherCourse: (state,action)=>{
            state.teacherCourses = action.payload
        }
    }
})

export const getTeacherCourses = (id) => apiCall({
    url: 'teacher-courses/'+id,
    method: 'GET',
    onSuccess: slice.actions.getAllTeacherCourse.type
})



export default slice.reducer