import {configureStore} from '@reduxjs/toolkit'
import {api} from "./middleware/api";
import user from './users'
import teacher from "./teacher/teacher";
import login from "./login";
import unApproveTeacher from "./teacher/unApproveTeachers";
import getStudentCourse from "./student/getStudentCourses";
import getTeacherCourse from "./teacher/getTeacherCourses";
import course from "./course/course";


export default configureStore({
    reducer: {
        user, teacher, login, unApproveTeacher, getStudentCourse, getTeacherCourse, course
    },
    middleware: [
        api,
    ]
})