import {createSlice} from "@reduxjs/toolkit";
import {apiCall} from "./apiAction";

const slice = createSlice({
    name: 'user',
    initialState: {users: []},
    reducers:{
        getAllUser: (state,action)=>{
            state.users = action.payload
        },
        saveUser: (state,action)=>{
            state.users.unshift(action.payload)
        },
        editUser: (state,action)=>{
            state.users = state.users.map(item=>item.id === action.payload.id ? action.payload : item)
        },
        deletedUser: (state,action)=>{
            state.users = state.users.filter(item=>item.id !== action.payload)
        }
    }
})

export const getUsers = () => apiCall({
    url: 'users/',
    method: 'GET',
    onSuccess: slice.actions.getAllUser.type
})

export const userSave = (data) => apiCall({
    url: 'users/',
    method: 'POST',
    onSuccess: slice.actions.saveUser.type,
    data,
})

export const editUserSave = (data) => apiCall({
    url: 'users/'+data.id,
    method: 'PUT',
    onSuccess: slice.actions.editUser.type,
    data,
})

export const deleteUser = (data) => apiCall({
    url: 'users/'+data.id,
    method: 'DELETE',
    onSuccess: slice.actions.deletedUser.type,
})

export default slice.reducer