import {BagCheck} from 'react-bootstrap-icons'
import {Link} from "react-router-dom";

function Header() {
    return <div className={'container-fluid'}>
        <nav className="navbar navbar-expand-lg navbar-light bg-light px-5 py-3">
            <button className="navbar-toggler" type="button" data-toggle="collapse"
                    data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false"
                    aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"/>
            </button>

            <div className="navbar-collapse justify-content-between">
                <ul className="navbar-nav mr-auto">
                    <li className="nav-item active">
                        <Link className="nav-link text-primary" to={'/'}>Home</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link text-primary" to={'/courses/'}>All Courses</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link btn btn-outline-info text-dark" to={'/signUpTeacher/'}>Teach on Educative</Link>
                    </li>
                </ul>
                <div className={'d-flex'}>
                    <form className="d-flex my-2 my-lg-0">
                        <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search"/>
                        <button className="btn btn-outline-success mx-2" type="submit">Search</button>
                    </form>
                    <a className="text-decoration-none mx-2 d-flex align-items-center justify-content-center" href="/cart/">
                        <BagCheck/>
                        <span className="mx-1 number">0</span>
                    </a>
                    <Link className="btn btn-outline-success mx-2" to={'/signUp/'}>Sign Up</Link>
                    <Link className="btn btn-outline-primary mx-2" to={'/login/'}>Login</Link>
                </div>
            </div>
        </nav>
    </div>
}

export default Header