import {createSlice} from "@reduxjs/toolkit";
import {apiCall} from "../apiAction";

const slice = createSlice({
    name: 'course',
    initialState: {courses: []},
    reducers:{
        getAllCourse: (state,action)=>{
            state.courses = action.payload
        },
        saveCourse: (state,action)=>{
            state.courses.unshift(action.payload)
        },
        editCourse: (state,action)=>{
            state.courses = state.courses.map(item=>item.id === action.payload.id ? action.payload : item)
        }
    }
})

export const getCourses = () => apiCall({
    url: 'courses/',
    method: 'GET',
    onSuccess: slice.actions.getAllCourse.type
})

export const courseSave = (data) => apiCall({
    url: 'courses/',
    method: 'POST',
    onSuccess: slice.actions.saveCourse.type,
    data,
})

export const editCourseSave = (data) => apiCall({
    url: 'courses/'+data.id+'/',
    method: 'PATCH',
    onSuccess: slice.actions.editCourse.type,
    data,
})


export default slice.reducer