import AdminHeader from "../../blocks/adminHeader";
import {Link} from "react-router-dom";
import {useEffect, useState} from "react";
import {connect} from "react-redux";
import {getUnApproveTeachers} from "../../../store/teacher/unApproveTeachers";
import {editTeacherSave} from "../../../store/teacher/teacher";

function ApproveTeachers(props){

    useEffect(()=>{
        props.getUnApproveTeachers()
    }, [props.teacher.teachers])


    const {first_name, last_name, image} = props.login.user
    const {unApproveTeachers} = props.unApproveTeacher

    function activateTeacher(data){
        data = {id: data.id, is_active: true}
        props.editTeacherSave(data)
    }

    function deActivateTeacher(data){
        data = {id: data.id, is_approve: false}
        props.editTeacherSave(data)
    }


    return <div>
        <AdminHeader/>
        <div className="container">
            <div className="row p-4">
                <div className="col-md-12 p-4">
                    <div className="row">
                        <div className="col-md-3">
                            <div className="photo-user">
                                {image?<img src={image} alt="User"/>:""}
                            </div>
                        </div>
                        <div className="col-md-9 d-flex align-items-center">
                            <div className="name-user">
                                <h2>{first_name} {last_name}</h2>
                            </div>
                        </div>
                    </div>
                </div>
                <hr/>
                <div className="col-md-3 left-side-dashboard">
                    <ul className={'list-group'}>
                        <li className={'list-group-item text-primary'}><Link to={'/adminDashboard/'}>Dashboard</Link></li>
                        <li className={'list-group-item text-primary'}><Link to={'/adminDashboard/'}>Profile</Link></li>
                        <li className={'list-group-item text-primary'}><Link to={'/adminDashboard/'}>Logout</Link></li>
                    </ul>
                </div>
                <div className="col-md-9">
                    <h2 className={'text-center py-3'}>Unapproved Teachers List</h2>
                    <table className={'table-bordered table'}>
                        <thead>
                        <tr>
                            <th>First Name</th>
                            <th>Last Name</th>
                            <th>Action</th>
                        </tr>
                        </thead>
                        <tbody>
                        {unApproveTeachers.map((item,index)=><tr key={index}>
                            <td>{item.first_name}</td>
                            <td>{item.last_name}</td>
                            <td>
                                <button className={'btn btn-info mx-2'} onClick={()=>activateTeacher(item)}>Approve</button>
                                <button className={'btn btn-danger'} onClick={()=>deActivateTeacher(item)}>Reject</button>
                            </td>
                        </tr>)}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
}
export default connect(state => state, {getUnApproveTeachers,editTeacherSave})(ApproveTeachers)