import Header from '../blocks/header'
import {connect} from "react-redux";
import {useEffect} from "react";
import {getCourses} from "../../store/course/course";
import {Link} from "react-router-dom";

function AllCourses(props){

    useEffect(()=>{
        props.getCourses()
    },[])

    const {courses} = props.course

    return <div className={''}>
        <Header/>
        <div className={'courses container'}>
            <h1 className={'text-success text-center my-4'}>All Courses</h1>
            <div className="row">
                {courses.map(item=><div key={item.id} className="col-md-3">
                    <div className="card">
                        <div className="card-image">
                            <img src={item.photo} className={'w-100'} alt={item.title}/>
                        </div>
                        <div className={'card-body'}>
                            <div className="card-title">
                                <a><h5 className={'text-capitalize'}>{item.title}</h5>
                                    <b>${item.price}</b>
                                </a>
                                <hr/>
                                <Link className={'btn btn-primary w-100'} to={'#'}>View</Link>
                            </div>
                        </div>
                    </div>
                </div>)}
            </div>
        </div>
    </div>
}
export default connect(state=>state,{getCourses})(AllCourses)