import Header from "../blocks/header";
import {toast} from "react-toastify";
import {connect} from "react-redux";
import {teacherSave} from "../../store/teacher/teacher"
import {getUsers} from "../../store/users";
import {useEffect} from "react";
import {useNavigate} from "react-router-dom";


function SignUpTeacher(props){

    const navigate = useNavigate();

    useEffect(()=>{
        props.getUsers()
    }, [])


    function saveData(event){
        event.preventDefault()
        const user = {
            first_name: event.target[0].value,
            last_name: event.target[1].value,
            username: event.target[2].value,
            email: event.target[3].value,
            password: event.target[4].value,
            password1: event.target[5].value
        }
        if (user.password !== user.password1){
            return toast.error('Password confirmation is not the same!')
        }
        else if(props.user.users.filter(item=>item.username === user.username).length){
            return toast.error('Such a username is available! Please enter another username!')
        }
        else{
            props.teacherSave(user)
            toast.success('Successfully!')
            navigate('/login/')
        }
    }

    return <div>
        <Header/>
        <div className={'container-fluid'}>
            <div className="row justify-content-center my-5">
                <div className="col-md-4">
                    <h1 className={'text-center'}>Sign Up</h1>
                    <form className={'flex-column d-flex'} onSubmit={saveData}>
                        <label htmlFor="firstName" className={'form-label'}>First Name
                            <input type="text" id={'firstName'} className={'form-control'}/>
                        </label>
                        <label htmlFor="lastName" className={'form-label'}>Last Name
                            <input type="text" id={'lastName'} className={'form-control'}/>
                        </label>
                        <label htmlFor="userName" className={'form-label'}>Username*
                            <input type="text" id={'userName'} className={'form-control'} required/>
                        </label>
                        <label htmlFor="email" className={'form-label'}>Email address
                            <input type="email" id={'email'} className={'form-control'}/>
                        </label>
                        <label htmlFor="password1" className={'form-label'}>Password*
                            <input type="password" id={'password1'} className={'form-control'} required/>
                        </label>
                        <label htmlFor="password2" className={'form-label'}>Password confirmation*
                            <input type="password" id={'password2'} className={'form-control'} required/>
                        </label>
                        <button type={'submit'} className={'btn btn-outline-primary my-3'}>Sign Up</button>
                    </form>
                </div>
            </div>
        </div>
    </div>
}
export default connect(state=>state, {getUsers, teacherSave})(SignUpTeacher)