import {Link} from "react-router-dom";
import TeacherHeader from "../../blocks/teacherHeader";
import {useEffect} from "react";
import Header from "../../blocks/header";
import NotFound from "../../blocks/notFound";
import {connect} from "react-redux";
import {getTeacherCourses} from "../../../store/teacher/getTeacherCourses";

function Dashboard(props) {

    const {first_name, last_name, image, token, id} = props.login.user

    useEffect(()=>{
        props.getTeacherCourses(id)
    }, [props.login])

    const {course} = props.getTeacherCourse.teacherCourses

    return <div>
        {token?<TeacherHeader/>:<Header/>}
        {token?<div className="container">
            <div className="row p-4">
                <div className="col-md-12 p-4">
                    <div className="row">
                        <div className="col-md-3">
                            <div className="photo-user">
                                {image?<img src={image} alt="User"/>:""}
                            </div>
                        </div>
                        <div className="col-md-9 d-flex align-items-center">
                            <div className="name-user">
                                <h2>{first_name} {last_name}</h2>
                            </div>
                        </div>
                    </div>
                </div>
                <hr/>
                <div className="col-md-3 left-side-dashboard">
                    <ul className={'list-group'}>
                        <li className={'list-group-item text-primary'}><Link to={'/teacherDashboard/'}>Dashboard</Link></li>
                        <li className={'list-group-item text-primary'}><Link to={'/teacherDashboard/'}>Profile</Link></li>
                        <li className={'list-group-item text-primary'}><Link to={'/teacherDashboard/'}>Logout</Link></li>
                    </ul>
                </div>
                <div className="col-md-9">
                    <h2 className={'text-center py-3'}>Dashboard</h2>
                    <table className={'table-bordered table'}>
                        <thead>
                        <tr>
                            <th>#</th>
                            <th>Course Name</th>
                            <th>Students In Each Course</th>
                        </tr>
                        </thead>
                        <tbody>
                        {course?course.map((item,index)=><tr key={index}>
                            <td>{index+1}</td>
                            <td>{item.title}</td>
                            <td>{item.student.length}</td>
                        </tr>):<tr></tr>}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>:<NotFound/>}
    </div>
}

export default connect(state => state, {getTeacherCourses})(Dashboard)