import {Link} from "react-router-dom";
import {useEffect} from "react";
import {connect} from "react-redux";
import {getUnApproveTeachers} from "../../store/teacher/unApproveTeachers";
import {logoutUser} from "../../store/logout";


function AdminHeader(props) {

    useEffect(()=>{
        props.getUnApproveTeachers()
    }, [])

    const {unApproveTeachers} = props.unApproveTeacher

    return <div className={'container-fluid'}>
        <nav className="navbar navbar-expand-lg navbar-light bg-light px-5 py-3">
            <button className="navbar-toggler" type="button" data-toggle="collapse"
                    data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false"
                    aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"/>
            </button>

            <div className="navbar-collapse justify-content-between">
                <ul className="navbar-nav mr-auto">
                    <li className="nav-item active">
                        <Link className="nav-link text-primary" to={'/adminDashboard/'}>Home</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link text-primary" to={'/courses/'}>All Courses</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link text-primary" to={'/courses/'}>All Teachers</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link text-primary" to={'/courses/'}>All Students</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link btn-outline-primary btn" to={'/adminApproveList/'}>Unapproved Teachers
                            <span className={'bg-dark text-white px-2 mx-2'}>{unApproveTeachers.length}</span></Link>
                    </li>
                </ul>
                <div className={'d-flex'}>
                    <Link className="btn btn-primary" to={'#'}>Logout</Link>
                </div>
            </div>
        </nav>
    </div>
}

export default connect(state => state, {getUnApproveTeachers, logoutUser})(AdminHeader)