import Header from "../blocks/header";
import {Link, useNavigate} from "react-router-dom";
import {connect} from "react-redux";
import {loginUser} from "../../store/login";
import {useEffect} from "react";
import {toast} from "react-toastify";


function Login(props){

    const navigate = useNavigate()

    useEffect(()=>{
        redirect()
    }, [props.login.user])

    function saveData(event){
        event.preventDefault()
        const user = {
            username: event.target[0].value,
            password: event.target[1].value,
        }
        props.loginUser(user)
    }

    function redirect(){
        if(props.login.user.token){
            const {is_teacher, is_superuser} = props.login.user
            if(is_teacher){
                navigate('/teacherDashboard/')
            }
            else if(is_superuser){
                navigate('/adminDashboard/')
            }
            else{
                navigate('/studentDashboard/')
            }
        }
        else{
            toast.error(props.login.user.error)
        }
    }

    return <div>
        <Header/>
        <div className={'container-fluid'}>
            <div className="row justify-content-center my-5">
                <div className="col-md-4">
                    <h1 className={'text-center'}>Login</h1>
                    <form className={'flex-column d-flex'} onSubmit={saveData}>
                        <label htmlFor="userName" className={'form-label'}>UserName*
                            <input type="text" id={'userName'} className={'form-control'} required/>
                        </label>
                        <label htmlFor="password1" className={'form-label'}>Password*
                            <input type="password" id={'password1'} className={'form-control'} required/>
                        </label>
                        <button type={'submit'} className={'btn btn-outline-primary my-3'}>Login</button>
                        <Link to={'/signUp/'} className={'text-center'}>Sign Up</Link>
                    </form>
                </div>
            </div>
        </div>
    </div>
}
export default connect(state => state, {loginUser})(Login)