import {createSlice} from "@reduxjs/toolkit";
import {apiCall} from "../apiAction";

const slice = createSlice({
    name: 'studentCourse',
    initialState: {studentCourses: []},
    reducers:{
        getAllStudentCourse: (state,action)=>{
            state.studentCourses = action.payload
        }
    }
})

export const getStudentCourses = (id) => apiCall({
    url: 'student-courses/'+id,
    method: 'GET',
    onSuccess: slice.actions.getAllStudentCourse.type
})



export default slice.reducer