import {createSlice} from "@reduxjs/toolkit";
import {apiCall} from "../apiAction";

const slice = createSlice({
    name: 'teacher',
    initialState: {teachers: []},
    reducers:{
        getAllTeacher: (state,action)=>{
            state.teachers = action.payload
        },
        saveTeacher: (state,action)=>{
            state.teachers.unshift(action.payload)
        },
        editTeacher: (state,action)=>{
            state.teachers = state.teachers.map(item=>item.id === action.payload.id ? action.payload : item)
        }
    }
})

export const getTeachers = () => apiCall({
    url: 'teachers/',
    method: 'GET',
    onSuccess: slice.actions.getAllTeacher.type
})

export const teacherSave = (data) => apiCall({
    url: 'teacher-save/',
    method: 'POST',
    onSuccess: slice.actions.saveTeacher.type,
    data,
})

export const editTeacherSave = (data) => apiCall({
    url: 'teachers/'+data.id+'/',
    method: 'PATCH',
    onSuccess: slice.actions.editTeacher.type,
    data,
})


export default slice.reducer