import {createSlice} from "@reduxjs/toolkit";
import {apiCall} from "../apiAction";

const slice = createSlice({
    name: 'unApproveTeacher',
    initialState: {unApproveTeachers: []},
    reducers:{
        getAllUnApproveTeacher: (state,action)=>{
            state.unApproveTeachers = action.payload
        }
    }
})

export const getUnApproveTeachers = () => apiCall({
    url: 'teachers-unapprove/',
    method: 'GET',
    onSuccess: slice.actions.getAllUnApproveTeacher.type
})



export default slice.reducer