import React from 'react';
import ReactDOM from 'react-dom/client';
import {Routes, Route, BrowserRouter} from "react-router-dom";
import {Provider} from "react-redux";
import store from './store/store'
import App from './App';
import AllCourses from "./components/pages/all-courses";
import SignUp from "./components/pages/signUp";
import Login from "./components/pages/login";
import reportWebVitals from './reportWebVitals';
import 'bootstrap/dist/css/bootstrap.min.css'
import AdminDashboard from "./components/pages/admin/dashboard";
import ApproveTeachers from "./components/pages/admin/approveTeachers";
import StudentDashboard from "./components/pages/student/dashboard";
import TeacherDashboard from "./components/pages/teacher/dashboard";
import SignUpTeacher from "./components/pages/signUpTeacher";
import {ToastContainer} from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import * as PropTypes from "prop-types";

const root = ReactDOM.createRoot(document.getElementById('root'));

function Switch(props) {
    return null;
}

Switch.propTypes = {children: PropTypes.node};
root.render(
    <React.StrictMode>
        <BrowserRouter>
            <Provider store={store}>
                <Routes>
                    <Switch>
                        <Route path={'/'} element={<App/>}/>
                        <Route path={'/courses/'} element={<AllCourses/>}/>
                        <Route path={'/signUp/'} element={<SignUp/>}/>
                        <Route path={'/signUpTeacher/'} element={<SignUpTeacher/>}/>
                        <Route path={'/login/'} element={<Login/>}/>
                        <Route path={'/adminDashboard/'} element={<AdminDashboard/>}/>
                        <Route path={'/adminApproveList/'} element={<ApproveTeachers/>}/>
                        <Route path={'/studentDashboard/'} element={<StudentDashboard/>}/>
                        <Route path={'/teacherDashboard/'} element={<TeacherDashboard/>}/>
                    </Switch>
                </Routes>
            </Provider>
            <ToastContainer/>
        </BrowserRouter>
    </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
