import {createSlice} from "@reduxjs/toolkit";
import {apiCall} from "./apiAction";

const slice = createSlice({
    name: 'login',
    initialState: {user: {}},
    reducers:{
        userLogin: (state,action)=>{
            state.user = action.payload
        },
        errorLogin:(state,action)=>{
            state.user.error = 'Not Found!'
        },
    }
})

export const loginUser = (data) => apiCall({
    url: 'login/',
    method: 'POST',
    onSuccess: slice.actions.userLogin.type,
    onFail: slice.actions.errorLogin.type,
    data,
})



export default slice.reducer