import AdminHeader from "../../blocks/adminHeader";
import {Link} from "react-router-dom";
import {useEffect} from "react";
import {getTeachers} from "../../../store/teacher/teacher";
import {connect} from "react-redux";
import Header from "../../blocks/header";
import NotFound from "../../blocks/notFound";


function Dashboard(props) {

    useEffect(()=>{
        props.getTeachers()
    }, [])


    const {first_name, last_name, image, token} = props.login.user
    const {teachers} = props.teacher

    return <div>
        {token?<AdminHeader/>:<Header/>}
        {token?<div className="container">
            <div className="row p-4">
                <div className="col-md-12 p-4">
                    <div className="row">
                        <div className="col-md-3">
                            <div className="photo-user">
                                {image?<img src={image} alt="User"/>:""}
                            </div>
                        </div>
                        <div className="col-md-9 d-flex align-items-center">
                            <div className="name-user">
                                <h2>{first_name} {last_name}</h2>
                            </div>
                        </div>
                    </div>
                </div>
                <hr/>
                <div className="col-md-3 left-side-dashboard">
                    <ul className={'list-group'}>
                        <li className={'list-group-item text-primary'}><Link to={'/adminDashboard/'}>Dashboard</Link></li>
                        <li className={'list-group-item text-primary'}><Link to={'/adminDashboard/'}>Profile</Link></li>
                        <li className={'list-group-item text-primary'}><Link to={'/adminDashboard/'}>Logout</Link></li>
                    </ul>
                </div>
                <div className="col-md-9">
                    <h2 className={'text-center py-3'}>Dashboard</h2>
                    <table className={'table-bordered table'}>
                        <thead>
                        <tr>
                            <th>Teacher Name</th>
                            <th>Course Name</th>
                            <th>Students In Each Course</th>
                        </tr>
                        </thead>
                        <tbody>
                        {teachers.map((item,Tindex)=><tr key={Tindex}>
                            <td>{item.first_name} {item.last_name}</td>
                            <td>{item.course.map((cour, index)=><p key={index}>#{index+1} - <Link to={'#'}>{cour.title}</Link></p>)}</td>
                            <td>{item.course.map(crs => <p key={crs.id}>{crs.student.length}</p>)}</td>
                        </tr>)}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>:<NotFound/>}
    </div>
}

export default connect(state => state, {getTeachers})(Dashboard)